import { Block, Controller } from '@ali/4ever-cangjie';
import type { FoldableHeadingData } from '@ali/4ever-plugin-heading';

/**
 * 判断选区是否在折叠的区域内
 * @param controller
 * @param node 折叠标题节点
 * @returns
 */
export function isSelectionInFoldArea(controller: Controller, node: Block<FoldableHeadingData>) {
  const { document, selection } = controller.value;
  const end = selection.getEnd(document);
  const endBlock = document.getFurthsestBlock(end.key);

  if (endBlock === node) {
    return false;
  }

  return true;
}
